@import url("https://fonts.googleapis.com/css2?family=Calligraffitti&display=swap");

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: system-ui, sans-serif;
  background: linear-gradient(135deg, pink, violet);
  overflow: hidden;
}

/* üå∏ App container */
.app {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

/* üíå Main card */
.card {
  background: rgba(255, 255, 255, 0.35);
  backdrop-filter: blur(14px);
  border-radius: 20px;
  padding: 2rem 1.5rem;
  width: min(90%, 420px);
  text-align: center;
  z-index: 2;
}

/* Text */
.pre-text {
  font-size: 1.1rem;
  font-weight: 600;
}

.title {
  font-family: "Calligraffitti", cursive;
  font-size: clamp(2.5rem, 8vw, 4rem);
  margin: 0.5rem 0 2rem;
}

/* Buttons */
.button-area {
  position: relative;
  height: 160px;
}

.btn {
  border: none;
  border-radius: 14px;
  padding: 12px 22px;
  font-size: 1rem;
  font-weight: 700;
  cursor: pointer;
  touch-action: manipulation;
}

.yes {
  background: #ff5a8f;
  color: white;
  box-shadow: 0 8px 20px rgba(255, 90, 143, 0.4);
}

.no {
  position: absolute;
  background: #ffe6e6;
  color: #111;
}

/* üíñ Floating hearts */
.heart {
  position: absolute;
  top: -10vh;
  color: rgb(70, 0, 70);
  font-size: 22px;
  animation: fall linear forwards;
}

@keyframes fall {
  to {
    transform: translateY(120vh);
  }
}

/* üíù Modal */
.modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 50;
}

.modal {
  background: white;
  padding: 1.5rem;
  border-radius: 14px;
  width: min(90%, 360px);
  text-align: center;
  animation: pop 0.25s ease;
}

@keyframes pop {
  from {
    transform: scale(0.85);
    opacity: 0;
  }
}

.modal label {
  display: block;
  margin: 0.6rem 0;
}

.submit {
  margin-top: 1rem;
  background: #ff5a8f;
  color: white;
}

.close {
  position: absolute;
  top: 8px;
  right: 12px;
  border: none;
  background: none;
  font-size: 22px;
  cursor: pointer;
}

/* üñ•Ô∏è Desktop tweaks */
@media (min-width: 768px) {
  .button-area {
    height: 200px;
  }
}


.radio-option {
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin: 10px 0;
  text-align: left;
}

.inline-input {
  margin-left: 24px;
  padding: 6px 8px;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 0.9rem;
}


.event-selector {
  display: flex;
  justify-content: center;
  margin-bottom: 16px;
}

.event-dropdown {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;

  padding: 10px 16px;
  padding-right: 40px;
  font-size: 14px;
  font-weight: 500;

  border-radius: 999px;
  border: 1px solid #f0c0d0;
  background-color: #fff;

  color: #7a1336;
  cursor: pointer;

  background-image: url("data:image/svg+xml;utf8,<svg fill='%237a1336' height='20' viewBox='0 0 20 20' width='20' xmlns='http://www.w3.org/2000/svg'><path d='M5.5 7l4.5 5 4.5-5z'/></svg>");
  background-repeat: no-repeat;
  background-position: right 14px center;

  transition: all 0.2s ease;
}

.event-dropdown:hover {
  border-color: #e11d48;
}

.event-dropdown:focus {
  outline: none;
  border-color: #e11d48;
  box-shadow: 0 0 0 2px rgba(225, 29, 72, 0.2);
}
